
const paths = require.context('./', true, /\.json$/).keys();
// 按照文件名中的数字顺序进行排序
paths.sort((a, b) => {
    const numberA = parseInt(a.match(/\d+/), 10);  // 提取文件名中的数字
    const numberB = parseInt(b.match(/\d+/), 10);  // 提取文件名中的数字
    return numberA - numberB;  // 按照数字进行升序排序
});

let content = []
for (let i = 0; i < paths.length; i++) {
    const file = require(`${paths[i]}`)
    content = content.concat({doc:file,path:encodeURIComponent(paths[i]),pathOri:paths[i]})
}
export const Data = content



