<template>
  <div>
    <el-row v-if="$store.state.lang == '繁体中文'" :gutter="10">
      <el-col :xs="24" :sm="20" :md="18" :lg="22" :xl="20">
        <div>
          <!-- 顶部信息 -->
          <el-col style="margin-top:5px;" :xs="24" :sm="24" :md="24" :lg="24" :xl="24">
            <div v-if="content && content.api" style="display: block;">
              <el-link style="margin: 15px 0 15px; color: black;">
                {{ content.api.uri }} ({{ content.title }})
              </el-link>
              <div>請求類型: {{ content.api.method }}</div>

              <div v-if="content.api['rate-limitation']">
                uid频率限制: {{ content.api['rate-limitation'] }} per account
              </div>

              <div v-if="content.api['ip-rate-limitation']">
                IP限速说明：
                <span v-if="content.api['ip-rate-limitation'] === '1'">
                  行情接口分組[1]: 分組內所有接口基於IP限頻總和: 500/10s, 1000/min
                </span>
                <span v-if="content.api['ip-rate-limitation'] === '2'">
                  賬號接口分組[2]: 分組內所有接口基於IP限頻總和: 2000/10s, 單個接口基於IP限頻: 300/10s
                </span>
                <span v-if="content.api['ip-rate-limitation'] === '3'">
                  賬號接口分組[3]: 分組內所有接口基於IP限頻總和: 2000/10s, 單個接口基於IP限頻: 300/10s
                </span>
              </div>

              <div v-for="(lineData, idx) in (content.info && content.info.zh ? content.info.zh : [])"
                   :key="'zh-desc-'+idx">
                {{ lineData.info }}
              </div>
            </div>
            <div v-else>
              <p style="color: gray; padding: 20px;">No API content found for this path.</p>
            </div>
          </el-col>

          <!-- 请求地址 -->
          <el-col style="margin-top:5px;" :xs="24" :sm="24" :md="24" :lg="24" :xl="24">
            <div ref="请求地址">
              <el-link style="margin: 15px 0 15px; color: black;">请求地址</el-link>
              <el-table header-height="30px" :data="tableData" border width="100%" :row-class-name="tableRowClassName">
                <el-table-column prop="env" label="环境" width="180" />
                <el-table-column prop="address" label="地址" />
              </el-table>
            </div>
          </el-col>

          <!-- 请求参数 -->
          <el-col style="margin-top:5px;" :xs="24" :sm="24" :md="24" :lg="24" :xl="24">
            <div ref="请求参数" style="padding-bottom: 20px;">
              <el-link style="margin: 15px 0 15px; color: black;">请求参数</el-link>
              <el-table header-height="30px" :data="restApiRequest" border :row-class-name="tableRowClassName">
                <el-table-column prop="name" label="请求参数" width="120" />
                <el-table-column prop="type" label="数据类型" width="80" />
                <el-table-column prop="required_zh" label="是否必填" width="80" />
                <el-table-column prop="zh" label="描述" width="280" />
                <el-table-column prop="values" label="取值范围" />
              </el-table>
              <div v-if="content && content.api">{{ content.api.request && content.api.request.info }}</div>
            </div>
          </el-col>

          <!-- 响应参数 -->
          <el-col style="margin-top:5px;" :xs="24" :sm="24" :md="24" :lg="24" :xl="24">
            <div ref="响应参数" style="padding-bottom: 20px;">
              <el-link style="margin: 15px 0 15px; color: black;">响应参数</el-link>
              <el-table header-height="30px" :data="restApiResponse" border :row-class-name="tableRowClassName">
                <el-table-column prop="name" label="请求参数" width="120" />
                <el-table-column prop="type" label="数据类型" width="80" />
                <el-table-column prop="zh" label="描述" width="280" />
                <el-table-column prop="values" label="取值范围" />
              </el-table>
              <div v-if="content && content.api">{{ content.api.response && content.api.response.info }}</div>
            </div>
          </el-col>

          <!-- 请求示例 -->
          <el-col style="margin-top:5px;" :xs="24" :sm="24" :md="24" :lg="24" :xl="24">
            <div ref="请求示例">
              <el-link style="margin: 15px 0 15px; color: black;">请求示例</el-link>
              <div style="min-height: 300px; overflow: auto;" class="code-html demo-code">
                <pre><code style="height: 20px;" v-html="payloadHtml" :class="['hljs', 'language-'+activeCodeTab]" /></pre>
              </div>
            </div>
          </el-col>

          <!-- 响应示例 -->
          <el-col style="margin-top:5px;" :xs="24" :sm="24" :md="24" :lg="24" :xl="24">
            <div ref="响应示例">
              <el-link style="margin: 15px 0 15px; color: black;">响应示例</el-link>
              <div style="min-height: 600px; overflow: auto;" class="code-html demo-code">
                <pre><code v-html="responseBodyHtml" :class="['hljs', 'language-'+activeCodeTab]" /></pre>
              </div>
            </div>
          </el-col>

          <!-- 错误码解释 -->
          <el-col style="margin-top:5px;" :xs="24" :sm="24" :md="24" :lg="24" :xl="24">
            <div style="padding-bottom: 20px;" ref="错误码解释">
              <el-link style="margin: 15px 0 15px; color: black;">错误码解释</el-link>
              <el-table header-height="30px" :data="errorList"  :row-class-name="tableRowClassName">
                <el-table-column prop="code" label="请求参数" width="120" />
                <el-table-column prop="msg" label="錯誤信息" width="380" />
              </el-table>
              <div v-if="content && content.api">{{ content.api.error && content.api.error.info }}</div>
            </div>
          </el-col>

          <!-- 代码示例 -->
          <el-col style="margin-top:5px;" :xs="24" :sm="24" :md="24" :lg="24" :xl="24">
            <div ref="代码示例">
              <el-link style="margin: 15px 0 15px; color: black;">代码示例</el-link>
              <div class="code-html demo-code">
                <pre><code style="height: 20px;" v-html="payloadHtml" :class="['hljs', 'language-'+activeCodeTab]" /></pre>
              </div>
            </div>
          </el-col>
        </div>
      </el-col>

      <!-- 右侧锚点 -->
      <el-col :xs="2" :sm="2" :md="3" :lg="1" :xl="2">
        <div class="fixed-sidebar">
          <el-tabs @tab-click="scrollToElement" tab-position="right" style="min-height: 500px; text-align: left;">
            <el-tab-pane label="请求地址"></el-tab-pane>
            <el-tab-pane label="请求参数"></el-tab-pane>
            <el-tab-pane label="响应参数"></el-tab-pane>
            <el-tab-pane label="请求示例"></el-tab-pane>
            <el-tab-pane label="响应示例"></el-tab-pane>
            <el-tab-pane label="错误码解释"></el-tab-pane>
            <el-tab-pane label="代码示例"></el-tab-pane>
          </el-tabs>
        </div>
      </el-col>
    </el-row>

    <!-- 英文版 -->
    <el-row v-else :gutter="10">
      <el-col :xs="24" :sm="20" :md="18" :lg="22" :xl="20">
        <div>
          <!-- 顶部信息 -->
          <el-col style="margin-top:5px;" :xs="24" :sm="24" :md="24" :lg="24" :xl="24">
            <div v-if="content && content.api" style="display: block;">
              <el-link style="margin: 15px 0 15px; color: black;">
                {{ content.api.method }} {{ content.api.uri }} ({{ content.en }})
              </el-link>

              <div v-if="content.api['rate-limitation']">
                rate limitation: {{ content.api['rate-limitation'] }} per UID
              </div>

              <div v-if="content.api['ip-rate-limitation']">
                <span v-if="content.api['ip-rate-limitation'] === '1'">
                  Market API Group [1]: The total IP rate limit for all interfaces within the group is 500 requests per 10 seconds and 1000 requests per minute.
                </span>
                <span v-if="content.api['ip-rate-limitation'] === '2'">
                  Account API Group [2]: The total IP rate limit for all interfaces within the group is 2000 requests per 10 seconds, with an individual IP rate limit of 300 requests per 10 seconds for each interface.
                </span>
                <span v-if="content.api['ip-rate-limitation'] === '3'">
                  Account API Group [3]: The total IP rate limit for all interfaces within the group is 2000 requests per 10 seconds, with an individual IP rate limit of 300 requests per 10 seconds for each interface.
                </span>
              </div>

              <div v-for="(lineData, idx) in (content.info && content.info.en ? content.info.en : [])"
                   :key="'en-desc-'+idx">
                {{ lineData.info }}
              </div>
            </div>
          </el-col>

          <!-- host -->
          <el-col style="margin-top:5px;" :xs="24" :sm="24" :md="24" :lg="24" :xl="24">
            <div ref="host">
              <el-link style="margin: 15px 0 15px; color: black;">host</el-link>
              <el-table header-height="30px" :data="tableData" border width="100%" :row-class-name="tableRowClassName">
                <el-table-column prop="env" label="PROD" width="180" />
                <el-table-column prop="address" label="host" />
              </el-table>
            </div>
          </el-col>

          <!-- request parameter -->
          <el-col style="margin-top:5px;" :xs="24" :sm="24" :md="24" :lg="24" :xl="24">
            <div ref="request parameter" style="padding-bottom: 20px;">
              <el-link style="margin: 15px 0 15px; color: black;">request parameter</el-link>
              <el-table header-height="30px" :data="restApiRequest" border :row-class-name="tableRowClassName">
                <el-table-column prop="name" label="request parameter" width="120" />
                <el-table-column prop="type" label="data type" width="120" />
                <el-table-column prop="required_en" label="required" width="80" />
                <el-table-column prop="en" label="description" width="280" />
                <el-table-column prop="values_en" label="values" />
              </el-table>
              <div v-if="content && content.api">{{ content.api.request && content.api.request.info_en }}</div>
            </div>
          </el-col>

          <!-- response body -->
          <el-col style="margin-top:5px;" :xs="24" :sm="24" :md="24" :lg="24" :xl="24">
            <div ref="response body" style="padding-bottom: 20px;">
              <el-link style="margin: 15px 0 15px; color: black;">response body</el-link>
              <el-table header-height="30px" :data="restApiResponse" border :row-class-name="tableRowClassName">
                <el-table-column prop="name" label="filed" width="120" />
                <el-table-column prop="type" label="data type" width="120" />
                <el-table-column prop="en" label="description" width="280" />
                <el-table-column prop="values_en" label="values" />
              </el-table>
              <div v-if="content && content.api">{{ content.api.response && content.api.response.info_en }}</div>
            </div>
          </el-col>

          <!-- request sample -->
          <el-col style="margin-top:5px;" :xs="24" :sm="24" :md="24" :lg="24" :xl="24">
            <div ref="request sample">
              <el-link style="margin: 15px 0 15px; color: black;">request sample</el-link>
              <div class="code-html demo-code">
                <pre><code style="height: 20px;" v-html="payloadHtml" :class="['hljs', 'language-'+activeCodeTab]" /></pre>
              </div>
            </div>
          </el-col>

          <!-- response sample -->
          <el-col style="margin-top:5px;" :xs="24" :sm="24" :md="24" :lg="24" :xl="24">
            <div ref="response sample">
              <el-link style="margin: 15px 0 15px; color: black;">response sample</el-link>
              <div class="code-html demo-code">
                <pre><code style="height: 20px;" v-html="responseBodyHtml" :class="['hljs', 'language-'+activeCodeTab]" /></pre>
              </div>
            </div>
          </el-col>

          <!-- error code -->
          <el-col style="margin-top:5px;" :xs="24" :sm="24" :md="24" :lg="24" :xl="24">
            <div style="padding-bottom: 20px;" ref="error code">
              <el-link style="margin: 15px 0 15px; color: black;">error code</el-link>
              <el-table header-height="30px" :data="errorList" border :row-class-name="tableRowClassName">
                <el-table-column prop="code" label="code" width="120" />
                <el-table-column prop="msg" label="error msg" width="380" />
              </el-table>
              <div v-if="content && content.api">{{ content.api.error && content.api.error.info_en }}</div>
            </div>
          </el-col>

          <!-- demo code -->
          <el-col style="margin-top:5px;" :xs="24" :sm="24" :md="24" :lg="24" :xl="24">
            <div ref="demo code">
              <el-link style="margin: 15px 0 15px; color: black;">demo code</el-link>
              <div class="code-html demo-code">
                <pre><code style="height: 20px;" v-html="payloadHtml" :class="['hljs', 'language-'+activeCodeTab]" /></pre>
              </div>
            </div>
          </el-col>
        </div>
      </el-col>

      <!-- 右侧锚点 -->
      <el-col :xs="4" :sm="4" :md="4" :lg="2" :xl="4">
        <div class="fixed-sidebar">
          <el-tabs @tab-click="scrollToElement" tab-position="right" style="min-height: 500px; text-align: left;">
            <el-tab-pane label="host"></el-tab-pane>
            <el-tab-pane label="request parameter"></el-tab-pane>
            <el-tab-pane label="response body"></el-tab-pane>
            <el-tab-pane label="request sample"></el-tab-pane>
            <el-tab-pane label="response sample"></el-tab-pane>
            <el-tab-pane label="error code"></el-tab-pane>
            <el-tab-pane label="demo code"></el-tab-pane>
          </el-tabs>
        </div>
      </el-col>
    </el-row>
  </div>
</template>

<style>
.code-html {
  color: #FFFFFF;
  background-color: #282c34;
  overflow: hidden;
}
.code-html pre {
  margin: 0;
  padding: 0;
}
.code-html code {
  height: 60px;
  overflow: auto;
}
.el-table .warning-row {
  background: oldlace;
}
.code-html {
  height: 200px;
  margin: 0;
}
.el-table .success-row {
  background: #f0f9eb;
}
.el-table .el-table__header-wrapper tr {
  height: 20px;
  line-height: 20px;
}
.el-table .el-table__header-wrapper th {
  height: 25px;
  line-height: 25px;
  background-color: #eaedf0;
  color: black;
}
.fixed-sidebar {
  position: fixed;

}
</style>

<script>
export default {
  name: 'RestfulApi',
  props: {
    // 父组件传入完整文档对象：{ title, en, info, api: { request/response/error/payload/responseBody/... } }
    content: { type: Object, default: () => ({}) },
    // 可选：默认代码高亮语言（和 hljs 的 language-xxx 对齐）
    codeLang: { type: String, default: 'json' },
    // 可选：当 payload/responseBody 是对象时，是否自动 JSON.stringify
    stringify: { type: Boolean, default: true }
  },

  data() {
    return {
      // 代码示例 Tabs 的激活语言名（需和 class 'language-xxx' 对齐）
      activeCodeTab: this.codeLang,
      tableData: [
        { env: 'PROD', address: 'https://open-api.bingx.com' },
        { env: 'VST',  address: 'https://open-api-vst.bingx.com' }
      ]
    }
  },

  computed: {
    // ---- 下面全部从 props.content 安全派生（无 ?.） ----
    restApiRequest() {
      var api = (this.content && this.content.api) ? this.content.api : null
      var list = api && api.request && Array.isArray(api.request.list) ? api.request.list : []
      return list
    },
    restApiResponse() {
      var api = (this.content && this.content.api) ? this.content.api : null
      var list = api && api.response && Array.isArray(api.response.list) ? api.response.list : []
      return list
    },
    errorList() {
      var api = (this.content && this.content.api) ? this.content.api : null
      var list = api && api.error && Array.isArray(api.error.list) ? api.error.list : []
      return list
    },
    payloadRaw() {
      var api = (this.content && this.content.api) ? this.content.api : null
      return api && api.payload ? api.payload : ''
    },
    responseBodyRaw() {
      var api = (this.content && this.content.api) ? this.content.api : null
      return api && api.responseBody ? api.responseBody : ''
    },
    // 给 v-html 用：保证是字符串
    payloadHtml() {
      var v = this.payloadRaw
      if (typeof v === 'string') return v
      if (this.stringify) {
        try { return JSON.stringify(v, null, 2) } catch(e) { return String(v) }
      }
      return String(v)
    },
    responseBodyHtml() {
      var v = this.responseBodyRaw
      if (typeof v === 'string') return v
      if (this.stringify) {
        try { return JSON.stringify(v, null, 2) } catch(e) { return String(v) }
      }
      return String(v)
    }
  },

  methods: {
    scrollToElement(tab) {
      // 标签名与 ref 对应
      var label = tab && tab.label ? tab.label : ''
      var el = this.$refs[label]
      if (el && el.scrollIntoView) {
        el.scrollIntoView({ behavior: 'smooth' })
      }
    },
    tableRowClassName(args) {
      // 可按你真实数据结构调整：这里兼容 row.type / row.level
      var row = args && args.row ? args.row : null
      if (!row) return ''
      if (row.type === 'breaking' || row.level === 'breaking') return 'warning-row'
      if (row.type === 'fix' || row.level === 'success') return 'success-row'
      return ''
    }
  },

  watch: {
    codeLang(newVal) {
      // 父组件切换默认语言时，同步 tabs 选中
      this.activeCodeTab = newVal || 'json'
    }
  }
}
</script>



