    </el-row>

    <el-row v-else :gutter="10">
      <el-col :xs="8" :sm="21" :md="21" :lg="19" :xl="20">
        <div>
          <el-col style="margin-top: 5px;" :xs="8" :sm="20" :md="24" :lg="19" :xl="22">
            <div v-if="content.api">
              <el-link style="margin: 15px 0 15px; color: black;">{{ content.en }}</el-link>
              <div v-for="(lineData, i) in content.info.en" :key="i">{{ lineData.info }}</div>
            </div>
          </el-col>

          <el-col style="margin-top: 5px;" :xs="8" :sm="20" :md="24" :lg="19" :xl="22">
            <div ref="subscription address">
              <el-link style="margin: 15px 0 15px; color: black;">subscription address</el-link>
              <el-table header-height="30px" :data="tableData" border width="120%" :row-class-name="tableRowClassName">
                <el-table-column prop="env" label="ENV" width="180" />
                <el-table-column prop="address" label="host" />
              </el-table>
            </div>
          </el-col>

          <el-col style="margin-top: 5px;" :xs="8" :sm="20" :md="24" :lg="19" :xl="22">
            <div ref="subscription parameters" style="padding-bottom: 20px;">
              <el-link style="margin: 15px 0 15px; color: black;">subscription parameters</el-link>
              <el-table header-height="30px" :data="wsRequest" border width="120%" :row-class-name="tableRowClassName">
                <el-table-column prop="name" label="subscription parameter" width="220" />
                <el-table-column prop="type" label="data type" width="120" />
                <el-table-column prop="en" label="description" width="380" />
                <el-table-column prop="values" label="values" />
              </el-table>
              <div v-if="content.api">{{ content.api.request.en }}</div>
            </div>
          </el-col>

          <el-col style="margin-top: 5px;" :xs="8" :sm="20" :md="24" :lg="19" :xl="22">
            <div ref="data update" style="padding-bottom: 20px;">
              <el-link style="margin: 15px 0 15px; color: black;">data update</el-link>
              <el-table header-height="30px" :data="wsResponse" border width="120%" :row-class-name="tableRowClassName">
                <el-table-column prop="name" label="arameter" width="120" />
                <el-table-column prop="type" label="data type" width="120" />
                <el-table-column prop="en" label="description" width="980" />
              </el-table>
              <div v-if="content.api">{{ content.api.response.en }}</div>
            </div>
          </el-col>

          <el-col style="margin-top: 5px;" :xs="8" :sm="20" :md="24" :lg="19" :xl="22">
            <div ref="subscribe sample">
              <el-link style="margin: 15px 0 15px; color: black;">subscribe sample</el-link>
              <div class="code-html demo-code">
                <pre><code style="height: 20px;" v-html="payload" :class="['hljs', `language-${activeCodeTab}`]" /></pre>
              </div>
            </div>
          </el-col>

          <el-col style="margin-top: 5px;" :xs="8" :sm="20" :md="24" :lg="19" :xl="22">
            <div ref="subscribe success sample">
              <el-link style="margin: 15px 0 15px; color: black;">subscribe success sample</el-link>
              <div class="code-html demo-code">
                <pre><code style="height: 20px;" v-html="responseBody" :class="['hljs', `language-${activeCodeTab}`]" /></pre>
              </div>
            </div>
          </el-col>

          <el-col style="margin-top: 5px;min-height: 200px;" :xs="8" :sm="20" :md="24" :lg="19" :xl="22">
