              <el-link style="margin: 15px 0 15px; color: black;">cancel subscribe sample</el-link>
              <div class="code-html demo-code">
                <pre><code style="height: 20px;" v-html="unsub_payload" :class="['hljs', `language-${activeCodeTab}`]" /></pre>
              </div>
            </div>
          </el-col>

          <el-col style="margin-top: 5px;" :xs="8" :sm="20" :md="24" :lg="19" :xl="22">
            <div style="padding-bottom: 20px;" ref="error code">
              <el-link style="margin: 15px 0 15px; color: black;">error code</el-link>
              <el-table header-height="30px" :data="errorList" border width="120%" :row-class-name="tableRowClassName">
                <el-table-column prop="code" label="error code" width="220" />
                <el-table-column prop="msg" label="error massage" width="120" />
                <el-table-column prop="description.en" label="description" width="480" />
                <el-table-column prop="solution.en" label="solution" width="480" />
              </el-table>
              <div v-if="content.api">{{ content.api.error.en }}</div>
            </div>
          </el-col>

          <el-col style="margin-top: 5px;" :xs="8" :sm="20" :md="24" :lg="19" :xl="22">
            <div ref="code demo">
              <el-link style="margin: 15px 0 15px; color: black;">code demo</el-link>
              <div class="code-html demo-code">
                <pre><code style="height: 20px;" v-html="payload" :class="['hljs', `language-${activeCodeTab}`]" /></pre>
              </div>
