<style>
.code-html {
  color: #ffffff;
  background-color: #282c34;
  overflow: hidden;
  height: 200px;
  margin: 0;
}
.code-html pre {
  margin: 0;
  padding: 0;
}
.demo-code {
  overflow: auto;
  min-height: 200px;
}
.code-html code {
  height: 60px;
  overflow: auto;
}
.el-table .warning-row {
  background: oldlace;
}
.el-table .success-row {
  background: #f0f9eb;
}
.el-table .el-table__header-wrapper tr {
  height: 20px;
  line-height: 20px;
}
.el-table .el-table__header-wrapper th {
  height: 25px;
  line-height: 25px;
  background-color: #eaedf0;
  color: black;
}
/* 固定右侧侧边栏 */
.fixed-sidebar {
  position: fixed;
}
</style>

<script>
import { docs as apiDocs } from '../Docs/index'

const enDoc = {}
const cnDoc = {}

apiDocs.forEach(menu => {
  menu.list.forEach(subMenub => {
    subMenub.docs.forEach(docObj => {
      if (docObj.pathOri == './概述.json') {
        const keyZH = '/zh-tw/info'
        const keyEN = '/en/info'
        cnDoc[keyZH] = docObj.doc
        enDoc[keyEN] = docObj.doc
      } else if (docObj.pathOri == './更新记录.json') {
        const keyZH = '/zh-tw/changes/logs'
        const keyEN = '/en/changes/logs'
        cnDoc[keyZH] = docObj.doc
        enDoc[keyEN] = docObj.doc
      } else {
        const keyZH = '/zh-tw' + '/' + menu.en + '/' + subMenub.en + '/' + encodeURIComponent(docObj.doc.title)
        const keyEN = '/en' + '/' + menu.en + '/' + subMenub.en + '/' + encodeURIComponent(docObj.doc.en)
        cnDoc[keyZH] = docObj.doc
        enDoc[keyEN] = docObj.doc
      }
    })
  })
})

export default {
  name: 'WebsocketDoc',

  // 不再使用 props.content；改为本地 state
  // props: { content: Object },

  data() {
    return {
      activeCodeTab: 'json',

      enDoc: {},
      cnDoc: {},

      content: {},

      unsub_payload: '',
      payload: '',
      responseBody: '',
      dataUpdateDemo: '',

      wsRequest: [],
      wsResponse: [],
      errorList: [],

      tableData: [
        { env: 'PROD', address: 'wss://open-api-swap.bingx.com/swap-market' },
        { env: 'VST', address: 'wss://open-api-swap.bingx.com/swap-market' }
      ]
    }
  },
  methods: {
    // Element-UI row-class-name 回调
    tableRowClassName({ row, rowIndex }) {
      // 按需返回 class；示例：错误行用 warning-row，高亮奇数行
      if (row && (row.status === 'error' || row.code === 'ERROR')) return 'warning-row'
      return rowIndex % 2 === 1 ? 'success-row' : ''
    },

    scrollToElement(tab) {
      this.$nextTick(() => {
        const el = this.$refs[tab.label]
        if (el && el.scrollIntoView) {
          el.scrollIntoView({ behavior: 'smooth' })
        }
      })
    },

    normalizeContent(raw) {
      const safe = raw || {}
      // info
      if (!safe.info) safe.info = {}
      if (!Array.isArray(safe.info.zh)) safe.info.zh = []
      if (!Array.isArray(safe.info.en)) safe.info.en = []

      // api
      if (!safe.api) safe.api = {}
      const api = safe.api

      // request
      if (!api.request) api.request = {}
      if (!Array.isArray(api.request.list)) api.request.list = []
      if (typeof api.request.info !== 'string') api.request.info = ''
      if (typeof api.request.en !== 'string') api.request.en = ''

      // response
      if (!api.response) api.response = {}
      if (!Array.isArray(api.response.list)) api.response.list = []
      if (typeof api.response.info !== 'string') api.response.info = ''
      if (typeof api.response.en !== 'string') api.response.en = ''

      // error
      if (!api.error) api.error = {}
      if (!Array.isArray(api.error.list)) api.error.list = []
      if (typeof api.error.info !== 'string') api.error.info = ''
      if (typeof api.error.en !== 'string') api.error.en = ''

      // code/demo 字段统一为字符串，便于 v-html
      const str = v => (typeof v === 'string' ? v : JSON.stringify(v || '', null, 2))
      api.payload = str(api.payload)
      api.unsub_payload = str(api.unsub_payload)
      api.responseBody = str(api.responseBody)
      api.dataUpdateDemo = str(api.dataUpdateDemo)

      return safe
    },

    hydrateFromRoute() {
      const path = this.$route.path
      const isZh = path.split('/')[1] === 'zh-tw'
      const raw = isZh ? this.cnDoc[path] : this.enDoc[path]

      this.content = this.normalizeContent(raw)

      const api = this.content.api
      // 列表数据
      this.wsRequest = api.request.list
      this.wsResponse = api.response.list
      this.errorList = api.error.list

      // v-html 使用的字符串
      this.payload        = api.payload
      this.unsub_payload  = api.unsub_payload
      this.responseBody   = api.responseBody
      this.dataUpdateDemo = api.dataUpdateDemo
    },
    updateAddressBasedOnRoute(path) {
      console.log('shi')
      console.log(path)
      console.log(path.includes('swap'))
      console.log(path.includes('spot'))
      console.log(path.includes('cswap'))
      let split = path.split('/');
      let businessPart;
      if (split.length >=2) {
        businessPart = split[2]
        console.log(businessPart)
        // 判断路由路径是否包含 swap 或 spot
        if (businessPart === 'swap') {
          this.tableData = [
            { env: 'PROD', address: 'wss://open-api-swap.bingx.com/swap-market' },
            { env: 'VST', address: 'wss://open-api-swap.bingx.com/swap-market' }
          ];
        } else if (businessPart === 'spot') {
          this.tableData = [
            { env: 'PROD', address: 'wss://open-api-ws.bingx.com/market' },
            { env: 'VST', address: '' }
          ];
        } else if (businessPart === 'cswap') {
          this.tableData = [
            { env: 'PROD', address: 'wss://open-api-cswap-ws.bingx.com/market' },
            { env: 'VST', address: '' }
          ];
        }
      }


    }
  },

  watch: {
    $route() {
      this.hydrateFromRoute()
    }
  },

  mounted() {
    // 注入预处理后的文档映射
    this.enDoc = enDoc
    this.cnDoc = cnDoc
    this.hydrateFromRoute()
    // 页面加载时检查当前路由
    this.updateAddressBasedOnRoute(this.$route.path);
  }
}
</script>



