                <svg style="margin-left: 10%;" width="20" height="20" viewBox="0 0 20 20" fill="none"
                     xmlns="http://www.w3.org/2000/svg">
                  <path
                      d="M10 1.75C14.5563 1.75 18.25 5.44365 18.25 10C18.25 14.5563 14.5563 18.25 10 18.25C5.44365 18.25 1.75 14.5563 1.75 10C1.75 5.44365 5.44365 1.75 10 1.75ZM7.43457 10.75C7.5124 12.4332 7.84795 13.9164 8.3291 14.999C8.60514 15.62 8.91419 16.0764 9.21973 16.3672C9.52129 16.6541 9.78391 16.7499 10 16.75C10.2161 16.7499 10.4796 16.6542 10.7812 16.3672C11.0867 16.0763 11.3959 15.6198 11.6719 14.999C12.153 13.9164 12.4886 12.4331 12.5664 10.75H7.43457ZM3.29297 10.75C3.55981 13.1633 5.0979 15.1916 7.22363 16.1523C7.1294 15.9779 7.04157 15.7954 6.95801 15.6074C6.38002 14.3068 6.01121 12.6093 5.93262 10.75H3.29297ZM14.0674 10.75C13.9888 12.6092 13.6209 14.3068 13.043 15.6074C12.9593 15.7956 12.8698 15.9777 12.7754 16.1523C14.9015 15.1917 16.4402 13.1636 16.707 10.75H14.0674ZM12.7754 3.84668C12.87 4.02156 12.9592 4.2041 13.043 4.39258C13.6209 5.69318 13.9888 7.39081 14.0674 9.25H16.707C16.4401 6.83629 14.9017 4.80725 12.7754 3.84668ZM10 3.25C9.78392 3.2501 9.52128 3.34591 9.21973 3.63281C8.9142 3.92362 8.60514 4.38006 8.3291 5.00098C7.84795 6.08356 7.5124 7.5668 7.43457 9.25H12.5664C12.4886 7.56685 12.153 6.08355 11.6719 5.00098C11.3959 4.38016 11.0867 3.92369 10.7812 3.63281C10.4796 3.34578 10.2161 3.25013 10 3.25ZM7.22363 3.84668C5.09772 4.8074 3.55983 6.83659 3.29297 9.25H5.93262C6.01121 7.39071 6.38002 5.69322 6.95801 4.39258C7.04169 4.20431 7.12925 4.02138 7.22363 3.84668Z"
