        <el-aside v-if="this.$store.state.lang == '繁体中文'" style="text-align: left;" width="17%" height="936px">
          <el-row style="width: 100%;" class="tac">
            <el-col :span="24">
              <el-menu router :key="activeIndexAside" :default-active="activeIndexAside" class="el-menu-vertical-demo" @open="handleOpen"
                       @close="handleClose">
                <el-menu-item index='/zh-tw/info'>
                  <i class="el-icon-s-home"></i>
                  <span slot="title">概述</span>
                </el-menu-item>
                <el-submenu v-if="menu.tittle != '概述' && menu.tittle != '更新记录'" :index="menu.en"
                            v-for="menu in this.apiDocs" :key="'zh-'+menu.en">
                  <template v-if="menu.tittle != '概述' && menu.tittle != '更新记录'" slot="title">
                    <i class="el-icon-document"></i>
                    <span>{{ menu.tittle }}</span>
                  </template>
                  <el-submenu v-if="menu.tittle != '概述' && menu.tittle != '更新记录'"
                              :index="menu.en + '/' + subMenu.en"
                              v-for="subMenu in menu.list" :key="'zh-'+menu.en+'/'+subMenu.en">
                    <template slot="title">{{ subMenu.tittle }}</template>
                    <el-menu-item v-for="docsObj in subMenu.docs"
                                  :key="'zh-'+menu.en+'/'+subMenu.en+'/'+docsObj.doc.title"
                                  :index="'/zh-tw/' + menu.en + '/' + subMenu.en + '/' + encodeURIComponent(docsObj.doc.title)">
                      {{
                        docsObj.doc.title
                      }}
                    </el-menu-item>
                  </el-submenu>
                </el-submenu>
                <el-menu-item index='/zh-tw/changes/logs'>
                  <i class="el-icon-refresh"></i>
                  <span slot="title">更新记录</span>
                </el-menu-item>
              </el-menu>
            </el-col>

          </el-row>
        </el-aside>
        <el-aside v-else style="text-align: left;" width="17%" height="936px">
          <el-row style="width: 100%;" class="tac">
            <el-col :span="24">
              <el-menu :key="activeIndexAside" router :default-active="activeIndexAside" class="el-menu-vertical-demo" @open="handleOpen"
                       @close="handleClose">
                <el-menu-item index='/en/info'>
                  <i class="el-icon-s-home"></i>
                  <span slot="title">Introduce</span>
                </el-menu-item>
                <el-submenu v-if="menu.tittle != '概述' && menu.tittle != '更新记录'" :index="menu.en"
                            v-for="menu in this.apiDocs" :key="'en-'+menu.en">
                  <template v-if="menu.tittle != '概述' && menu.tittle != '更新记录'" slot="title">
                    <i class="el-icon-document"></i>
                    <span>{{ menu.en }}</span>
                  </template>
                  <el-submenu v-if="menu.tittle != '概述' && menu.tittle != '更新记录'"
