<style>
#lang {
  border: 0 solid #DCDFE6;
  text-align: center;
}

body {
  background-color: rgb(248, 246, 246);

}

.el-header {
  text-align: center;
  background-color: white;
  height: 64px;
  border-bottom: 1.5px solid #e3e0e0;
}

.el-aside {
  margin-top: 0.5%;
  border-radius: 5px;
  text-align: center;
  background-color: white;
  line-height: normal; /* 去掉固定的 line-height */
  height: auto; /* 自动调整高度 */
}

.el-main {
  padding-top: 10px;
  border-radius: 5px;
  margin-top: 0.5%;
  margin-left: 1%;
  background-color: white;
  text-align: center;
  /* line-height: 160px; */
}

body > .el-container {
  margin-bottom: 400px;
}

.el-container:nth-child(5) .el-aside,
.el-container:nth-child(6) .el-aside {
  line-height: 260px;
}

.el-container:nth-child(7) .el-aside {
  line-height: 320px;
}

.el-col {
  border-radius: 4px;
}

.bg-purple-dark {
  background: #99a9bf;
}

.bg-purple {
  background: #d3dce6;
}

.bg-purple-light {
  background: #e5e9f2;
}

.grid-content {
  border-radius: 4px;
  min-height: 36px;
}

.el-input--small .el-input__inner {
  border-radius: 22px;
}
</style>
<script>
import { docs as apiDocs } from './Docs/index'

export default {
  components: {},

  data() {
    return {
      apiDocs: [],
      allContent: {},         // 本地：路径 -> { doc }（doc 内含 api 等）
      lang: 'English',        // 仅用于本组件内部逻辑；模板仍用 $store.state.lang
      activeIndexAside: this.$route.path,
      activeIndex: this.$route.path,
      restaurants: [],        // 搜索源：[{ path, value(uri) }]
      searchData: '',
      state: '',
      timeout: null
    }
  },

  watch: {
    '$route'(to) {
      const arr = to.path.split('/')
      this.lang = (arr[1] && arr[1] === 'zh-tw') ? '繁体中文' : 'English'
      // 同步到全局 store，仅用于你模板里的 v-if 判断
      this.$store.commit('changeLang', this.lang)

      // 更新 activeIndex
      this.activeIndex = to.path
      // 更新 activeIndexAside
      this.activeIndexAside= to.path

      // 路由变更后，重建搜索源（按语言过滤）
      this.restaurants = this.loadAll()
    }
  },

  mounted() {
    // 打印当前路由路径
    console.log('Current route path:', this.$route.path);

    // 打印当前语言状态
    console.log('Current language in store:', this.$store.state.lang);

    const arr = this.$route.path.split('/');
    this.lang = (arr[1] && arr[1] === 'zh-tw') ? '繁体中文' : 'English';
    this.$store.commit('changeLang', this.lang);

    // 打印语言状态变化后的值
    console.log('Language after update:', this.lang);

    // 打印 apiDocs 数据

    this.apiDocs = apiDocs;
    console.log('API Docs data:', this.apiDocs);

    this.allContent = this.buildAllContentIndex();
    console.log('allContent:', this.allContent);

    // 初始化菜单与搜索
    this.activeIndex = this.$route.path || '/';
    this.restaurants = this.loadAll();

    console.log('Restaurants (search source):', this.restaurants);
    console.log('activeIndex:', this.activeIndex);
    console.log('activeIndexAside:', this.activeIndexAside);
  },

  methods: {
    /** 将 Docs 构造成本地 allContent 索引：{ [path]: { doc } } */
    buildAllContentIndex() {
      const enMap = {}
      const zhMap = {}

      apiDocs.forEach(menu => {
        menu.list.forEach(subMenu => {
          subMenu.docs.forEach(docObj => {
            if (docObj.pathOri === './概述.json') {
              zhMap['/zh-tw/info'] = docObj.doc
              enMap['/en/info'] = docObj.doc
            } else if (docObj.pathOri === './更新记录.json') {
              zhMap['/zh-tw/changes/logs'] = docObj.doc
              enMap['/en/changes/logs'] = docObj.doc
            } else {
              const keyZH = `/zh-tw/${menu.en}/${subMenu.en}/${encodeURIComponent(docObj.doc.title)}`
              const keyEN = `/en/${menu.en}/${subMenu.en}/${encodeURIComponent(docObj.doc.en)}`
              zhMap[keyZH] = docObj.doc
              enMap[keyEN] = docObj.doc
            }
          })
        })
      })

      const all = {}
      Object.keys(enMap).forEach(p => { all[p] = { doc: enMap[p] } })
      Object.keys(zhMap).forEach(p => { all[p] = { doc: zhMap[p] } })
      return all
    },

    handleSearch() {
      console.log('Searching for:', this.input2)
    },
    handleOpen() { console.log('Menu opened') },
    handleClose() { console.log('Menu closed') },

    /** 语言下拉切换：只改 URL，不直接写 $store.state.lang */
    onChange(value) {
      console.log('activeIndexAside',this.activeIndexAside)
      const currentPath = this.$route.path
      const parts = currentPath.split('/')
      const langPart = parts[1]
      const titlePart = parts[parts.length - 1]

      let newLangSeg = (value === '繁体中文') ? 'zh-tw' : 'en'
      let newPath = currentPath.replace(`/${langPart}`, `/${newLangSeg}`)

      // info / changes/logs 之外需要替换末段标题
      if (!['/info', '/changes/logs'].some(seg => currentPath.includes(seg))) {
        const node = this.allContent[currentPath]
        if (!node || !node.doc) return

        const en = node.doc.en
        const zh = node.doc.title
        const replaceTitle = (value === '繁体中文') ? zh : en
        newPath = newPath.replace(titlePart, encodeURI(replaceTitle))
        console.log('newPath:', newPath)
      }

      // 更新路由并刷新文档内容
      this.$router.push(newPath).then(() => {
        // 强制刷新组件，确保新语言的内容被加载
        this.$forceUpdate();
      });
    },

    /** 构建搜索源（按当前语言过滤，抽取有 uri 的条目） */
    loadAll() {
      const langSeg = (this.$route.path.split('/')[1] || '').toLowerCase()
      const all = this.allContent

      return Object.keys(all)
          .map(path => {
            const doc = all[path] && all[path].doc
            const value = (doc && doc.api && doc.api.uri) ? doc.api.uri : 'unknown'

            return { path, value }
          })
          .filter(item => item && item.value !== 'unknown' && pathIncludesLang(item.path, langSeg))

      function pathIncludesLang(p, seg) {
        return (p.split('/')[1] || '').toLowerCase() === seg
      }
    },

    querySearchAsync(queryString, cb) {
      const list = this.restaurants
      const res = queryString
          ? list.filter(s => (s.value || '').toLowerCase().indexOf(queryString.toLowerCase()) === 0)
          : list

      clearTimeout(this.timeout)
      this.timeout = setTimeout(() => cb(res), 300 + Math.random() * 300)
    },

    handleSelect(item) {
      console.log('Selected menu item:', item);
      if (item && item.path) this.$router.push(item.path)
    }
  }
}
</script>



