import Vue from 'vue'
import Vuex from 'vuex'

// import { getLang, setLang } from '../utils/lang'

Vue.use(Vuex)

const store = new Vuex.Store({
  state: {
    lang: "",
    // 存储所有的 content 数据，键是路径或 ID，值是 content
    allContent: {},
    // 存储所有的 MainContent 信息
    content: {}, // 存储 content 数据
    restApiRequest: [],
    restApiResponse: [],
    errorList: [],
    payload: {},
    responseBody: {},
    uri: {}
  },
  mutations: {
    changeLang(state, lang) {
      state.lang = lang
    },
    // 存储指定路径的 content 数据
    setContent(state, { path, content }) {
      Vue.set(state.allContent, path, content);
    },
    // 更新当前 content 数据
    updateContent(state, content) {
      state.content = content;
    },
    // 更新 API 请求相关数据
    updateApiData(state, apiData) {
      state.restApiRequest = apiData.request.list;
      state.restApiResponse = apiData.response.list;
      state.errorList = apiData.error.list;
      state.payload = apiData.payload;
      state.responseBody = apiData.responseBody;
      state.uri = apiData.uri;
    },
    // 清空 API 数据
    resetApiData(state) {
      state.restApiRequest = [];
      state.restApiResponse = [];
      state.errorList = [];
      state.payload = {};
      state.responseBody = {};
      state.uri = {};
    }
  },
  actions: {
    changeLang({ commit }) {
      commit('changeLang')
    },
    updateMainContent({ commit }, { path, content }) {
      commit('setContent', { path, content });
    },
    // 更新 content 数据
    updateContent({ commit }, { path, content }) {
      commit('setContent', { path, content });
      commit('updateContent', content);
    },
    // 更新 API 数据
    updateApiData({ commit }, apiData) {
      commit('updateApiData', apiData);
    },
    // 重置 API 数据
    resetApiData({ commit }) {
      commit('resetApiData');
    }
  },
  getters: {
    // currentLang: state => state.lang
    // 获取当前的 content 数据
    currentContent: state => state.content,
    // 获取指定路径的 content 数据
    getContent: state => (path) => state.allContent[path]
  }
})

export default store



